<template>
    <div class="basket">
        <div class="basket__wrapper container">
            <p v-if="!servicesSum[duration]"
               class="notify"
            >
                Выберите хотя бы одну услугу
            </p>
            <p v-else-if="servicesSum[duration] && hasAddTrial"
               class="price"
            >
                {{subscription.freePeriod}} бесплатно,&nbsp;
                <span class="price_sum">
                    потом {{servicesSum[duration]}} <span class="ruble">
                        &#8381;
                    </span>
                    <span v-if="duration === 1"
                          class="price_dur"
                    >
                        /мес
                    </span>
                    <span v-if="duration === 12"
                          class="price_dur"
                    >
                        /год
                    </span>
                </span>
            </p>
            <p v-else
               class="price"
            >
                Стоимость подписки&nbsp;
                <span class="price_sum">
                    {{servicesSum[duration]}} <span class="ruble">
                        &#8381;
                    </span>
                    <span v-if="duration === 1"
                          class="price_dur"
                    >
                        /мес
                    </span>
                    <span v-if="duration === 12"
                          class="price_dur"
                    >
                        /год
                    </span>
                </span>
            </p>
            <Btn
                class="btn"
                :color="!selectedServicesList.length ? 'gray' : ''"
                :class="[{inactive : clicked === true}, {disabled:!selectedServicesList.length}]"
                @click="conditionalAction"
            >
                {{hasAddTrial ? "Попробовать бесплатно" : "Оформить подписку"}}
            </Btn>
            <p class="agreement">
                Я принимаю
                <a href="https://www.is74.ru/mobile_app/agreement.pdf"
                   @click="$store.dispatch('sendUserEvent', 'YardSubTerms')"
                >
                    условия подписки
                </a>
            </p>
        </div>
    </div>


    <ModalWindow v-show="showSubscribeModal"
                 @click.self="closeModal"
    >
        <template #title>
            {{ hasAddTrial ? "Бесплатный период" : "Подключить подписку?" }}
        </template>
        <template v-if="hasAddTrial"
                  #description
        >
            Пользуйтесь услугой бесплатно {{ subscription.freePeriod }}! Затем с лицевого счета или привязанной карты будет
            взиматься плата - {{ servicesSum[duration] }}
            <span class="ruble">
                &#8381;
            </span>/мес
        </template>
        <template v-else
                  #description
        >
            C лицевого счета или привязанной карты будет списываться&nbsp;{{servicesSum[duration]}}&nbsp;<span class="ruble">
                &#8381;
            </span>/мес
        </template>
        <template #footer>
            <Btn
                color="gray"
                small
                @click="closeModal"
            >
                Отменить
            </Btn>
            <Btn
                small
                :class="{ disabled : clicked }"
                @click="subscribe"
            >
                {{ hasAddTrial ? "Попробовать" : "Подключить" }}
            </Btn>
        </template>
    </ModalWindow>
    <ModalWindow v-show="subscribeSuccessModal"
                 @click.self="subscribeSuccessModal = false"
    >
        <template #title>
            <img src="../assets/images/modal_success.svg"
                 alt="error"
                 class="modal__header_img"
                 width="50"
                 height="50"
            />
            Подписка успешно подключена!
        </template>
        <template #footer>
            <Btn
                color="gray"
                class="successModalBtn"
                small
                @click="goToMyYard"
            >
                Готово
            </Btn>
        </template>
    </ModalWindow>

    <ModalWindow v-show="subscribeErrorModal"
                 @click.self="subscribeErrorModal = false"
    >
        <template #title>
            <img src="../assets/images/modal_error.svg"
                 alt="error"
                 class="modal__header_img"
                 width="50"
                 height="50"
            />
            Не удалось подключить подписку!
        </template>
        <template #footer>
            <Btn
                color="gray"
                class="successModalBtn"
                small
                @click="subscribeErrorModal = false"
            >
                Готово
            </Btn>
        </template>
    </ModalWindow>
</template>

<script>
    import { defineAsyncComponent, defineComponent } from "vue";
    import { postActivateSubscribe, sendSelectedServices } from "./ApiRequests";
    import { API, GET_SUBSCRIPTION } from "./Constants";

    export default defineComponent({
        name: "Basket",
        components: {
            ModalWindow: defineAsyncComponent(() => import("@/components/ModalWindow.vue")),
            Btn: defineAsyncComponent(() => import("@/components/Button.vue")),
        },
        props: {
            selectedServicesList: {
                type: Array,
                required: true,
            },
            servicesSum: {
                type: Object,
                required: true,
            },
            duration: {
                type: Number,
                required: true,
            },
        },
        data: () => {
            return {
                clicked: false,
                subscription: {},
                showSubscribeModal: false,
                subscribeSuccessModal: false,
                subscribeErrorModal: false,
            };
        },
        computed: {
            hasAddTrial() {
                return this.$store.state.abonentInfo.hasAddTrial;
            },
            abonentType() {
                return this.$store.state.abonentInfo.abonentType;
            },
            isNotAbonent() {
                return this.abonentType === "not_abonent";
            },
            payMethod() {
                if (this.$store.state.payMethod) {
                    return this.$store.state.payMethod;
                } else {
                    const method = this.isNotAbonent ? "card" : "ls";
                    this.$store.dispatch("changePayMethod", method);
                    return this.$store.state.payMethod;
                }
            },
        },
        mounted() {
            this.getSubscription()
                .then((subscription) => (this.subscription = subscription));
        },
        methods: {
            async getSubscription() {
                return fetch(GET_SUBSCRIPTION(), {
                    headers: {
                        Authorization: `Bearer ${API.TOKEN}`,
                    },
                })
                    .then(async (data) => {
                        if (data.status === 200) {
                            return data.json();
                        } else throw new Error(await data.text());
                    })
                    // eslint-disable-next-line no-console
                    .catch(console.error);
            },
            async subscribe() {
                const selectedServicesList = [];

                Object.keys(this.selectedServicesList).forEach((item) => {
                    selectedServicesList.push(+this.selectedServicesList[item]);
                });

                if (!this.hasAddTrial) this.$store.dispatch("sendUserEvent", "YardSubRepeatActivate");
                if (this.payMethod === "ls") this.$store.dispatch("sendUserEvent", "YardSubPersonalAccountActivate");

                const data = {
                    "serviceIds": selectedServicesList,
                    "periodMonth": this.duration,
                };
                const deepLink = `isapp://lk?screen=SubscriptionPay&sum=${this.hasAddTrial ? `1&trial=true` : this.servicesSum[this.duration]}`;

                if (!this.clicked) {
                    this.clicked = true;

                    const sendSelectedServicesStatus = await sendSelectedServices(data);

                    if (sendSelectedServicesStatus && this.servicesSum[this.duration]) {
                        if (this.abonentType !== "not_abonent" && this.payMethod === "ls") {
                            const activateSubscribe = await postActivateSubscribe();
                            this.showSubscribeModal = false;
                            if (activateSubscribe) {
                                this.subscribeSuccessModal = true;
                                this.$store.dispatch("sendUserEvent", "YardSubPersonalAccountSuccess");
                            } else {
                                this.subscribeErrorModal = true;
                                this.$store.dispatch("sendUserEvent", "YardSubPersonalAccountFail");
                            }
                        } else {
                            this.deepLinkImitator(deepLink);
                            setTimeout(this.closeModal, 2000);
                        }
                    }
                    setTimeout(() => this.clicked = false, 2000);
                }
            },
            // имитация клика по <a> для инициализации диплинка на мерчант
            deepLinkImitator(deepLink) {
                const link = document.createElement("a");
                link.setAttribute("href", deepLink);
                link.click();
            },
            closeModal() {
                this.showSubscribeModal = false;
                if (!this.hasAddTrial) this.$store.dispatch("sendUserEvent", "YardSubRepeatCancel");
                if (this.payMethod === "ls") this.$store.dispatch("sendUserEvent", "YardSubPersonalAccountCancel");

            },
            openModal() {
                this.showSubscribeModal = true;
                this.$store.dispatch("sendUserEvent", "YardSubPressOrder");

                if (!this.hasAddTrial) this.$store.dispatch("sendUserEvent", "YardSubRepeatView");
                if (this.payMethod === "ls") this.$store.dispatch("sendUserEvent", "YardSubPersonalAccountView");
            },
            goToMyYard() {
                this.deepLinkImitator("isapp://lk?screen=MyYardPaySuccess");
            },
            conditionalAction() {
                // для неабонентов у которых имеется триал по нажатию кнопки 
                // "Попробовать бесплатно" необходимо сразу выполнять подписку
                if (this.isNotAbonent && this.hasAddTrial) {
                    this.subscribe();
                } else {
                    this.openModal();
                }
            },
        
        },

    });
</script>

<style scoped>
.basket {
  width: 100%;
  display: flex;
  position: relative;
  align-content: center;
  justify-content: center;
  padding-top: 14px;
  box-shadow: 0px -0.5px 0px rgba(6, 16, 41, 0.1);
  box-sizing: border-box;
}

.price,
.notify {
  display: flex;
  min-height: 17px;
  margin-bottom: 12px;
  text-align: center;
  font-size: 16px;
  line-height: 20px;;
  font-weight: 600;
  letter-spacing: -0.26px;
}


.price_sum {
  font-size: inherit;
  line-height: inherit;
  font-weight: 500;
  color: #1D86FF;
}

.price_dur {
  font-weight: 500;
}

.btn {
  width: 100%;
  margin-bottom: 12px;
}

.btn.disabled, .btn.unselected {
  pointer-events: none;
}

.btn.unselected {
  background: #9B9FA9;
}

.agreement {
  font-size: 12px;
  font-weight: 600;
  line-height: 17px;
  letter-spacing: -0.2px;
  color: #061029;
  text-decoration: underline;
  opacity: 0.4;
}

.agreement a {
  font-size: 12px;
  font-weight: 600;
  line-height: 17px;
  letter-spacing: -0.2px;
  color: inherit;
}

.modal__header_img {
  width: 50px;
  height: 50px;
  margin: 0 auto 16px;
}

.successModalBtn {
  display: flex;
  margin: auto;
}

.disabled {
  pointer-events: none;
}
</style>
